.ProfileCard {
  background-color: #333333;
  border: 2px solid #121212;
  border-radius: 10px 10px 0px 0px;
  margin-top: 12px;
  padding: 10px;
  margin-bottom: 40px;
}

.ProfileImage {
  object-fit: cover;
  height: 90px;
  width: 90px;
  margin: 4px;
}

.Modal {
  margin-top: 10rem;
  color: #121212;
}

.ModalHeader {
  background-color: #28a745;
  color: #ffffff;
}

.ModalBody {
  background-color: #ffffff;
  color: #121212;
}

.ModalFooter {
  background-color: #ffffff;
}

@media screen and (max-width: 991px) {
  .ProfileImage {
    width: 90px;
    height: 90px;
  }
}
